header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============================== */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    /* padding: 1rem; */
}

.cta > .btn {
    padding: 0 0.75rem 0;
}

/* ============================================ */
.header__socials{
display: flex;
flex-direction: column;
align-items: center;
gap: 0.8rem;
position: absolute;
left: 0;
bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


/* ============================================= */
.me{
    background: linear-gradient(var(--color-bg-variant), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0rem 0rem ;
    overflow: hidden;
    padding: 5rem 1.5rem;
}


/* ================================================= */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* ============================================ */

@media screen and (max-width: 1024px){
    header{
        height: 68vh;

    }
}


@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }

    .header__socials, .scroll__down{
        display: none;
    }
}